import React from 'react'
import { Link } from 'react-router-dom';
import { Button } from './Button';
import './Footer.css';

function Footer() {
    return (
        <div className="footer-container">
            <section className="footer-subscription">
                <p className="footer-subscription-heading">Join the HNSU newsletter to receive our information</p>
                <p className="footer-subscription-text">Subcribe and practice now</p>
                <div className="input-areas">
                    <form>
                        <input 
                            type="email" 
                            name="email" 
                            placeholder="Your email"
                            className="footer-input"
                            
                        ></input>
                        <Button buttonStyle="btn--outline">Subcribe</Button>
                    </form>
                </div>
            </section>
            <div className="footer-links">
                <div className="footer-link-wrapper">
                    <div className="footer-link-items">
                        <h2>About Us</h2>
                        <Link to="/sign-up">How it works</Link>
                        <Link to="/">Testimonials</Link>
                        <Link to="/">Careers</Link>
                        <Link to="/">Investors</Link>
                        <Link to="/">Terms of Services</Link>
                    </div>
                    <div className="footer-link-items">
                        <h2>Contact Us</h2>
                        <Link to="/sign-up">How it works</Link>
                        <Link to="/">Testimonials</Link>
                        <Link to="/">Careers</Link>
                        <Link to="/">Investors</Link>
                        <Link to="/">Terms of Services</Link>
                    </div>
                </div>
                <div className='footer-link-wrapper'>
                    <div class='footer-link-items'>
                        <h2>Videos</h2>
                        <Link to='/'>Submit Video</Link>
                        <Link to='/'>Ambassadors</Link>
                        <Link to='/'>Agency</Link>
                        <Link to='/'>Influencer</Link>
                    </div>
                    <div class='footer-link-items'>
                        <h2>Social Media</h2>
                        <Link to='/'>Facebook</Link>
                        <Link to='/'>Youtube</Link>
                        <Link to='/'>Twitter</Link>
                    </div>
                </div>
            </div>
            <section className="social-media">
                <div className="social-media-wrap">
                    <div className="footer-logo">
                        <Link to="/" className="social-logo">
                            HNSU <i className="fas fa-book-open"/>
                        </Link>
                    </div>
                    <small className="website-rights">HNSU Â© 2021</small>
                    <div className="social-icons">
                        
                        <Link 
                            to="/" 
                            target="_blank" 
                            aria-label="Facebook" 
                            className="social-icon-link facebook"
                        >
                          <i className="fab fa-facebook-f" />      
                        </Link>
                        <Link 
                            to="/" 
                            target="_blank" 
                            aria-label="Youtube" 
                            className="social-icon-link youtube"
                        >
                          <i className="fab fa-youtube" />      
                        </Link>
                        <Link 
                            to="/" 
                            target="_blank" 
                            aria-label="Twitter" 
                            className="social-icon-link twitter"
                        >
                          <i className="fab fa-twitter" />      
                        </Link>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Footer
